import Vue from 'vue'
import Router from 'vue-router'
const  Poetry = r => require.ensure([], ()=>r(require('@/components/poetry/PoetryPage.vue')))
Vue.use(Router)
export default new Router({
  routes: [
    {
      path:'/',
      name:'PoetryPage',
      component:Poetry
    }
  ]
})



